        <UiButton class="main-slider__button">
          
        </UiButton>
        <UiLink
          href="#"
          class="main-slider__link--laptop"
        >
          
        </UiLink>
      </MainSlide>
    </Swiper>
    <div class="main-slider__nav">
      <SimpleArrowButton class="main-slider__arrow main-slider__arrow--prev" />
