<script>
import { Swiper } from 'vue-awesome-swiper';
import 'swiper/css/swiper.min.css';
import MainSlide from '@pages/MainPage/MainSlider/MainSlide/MainSlide';
import UiButton from '@ui/UiButton/UiButton';
