import SimpleArrowButton from '@components/Buttons/SimpleArrowButton/SimpleArrowButton';
import SectionTemplate from '@templates/SectionTemplate/SectionTemplate';

export default {
  name: 'MainSlider',
  components: {
    SectionTemplate,
    UiMobilePagination,
    UiLink,
    UiButton,
    MainSlide,
    Swiper,
    SimpleArrowButton,
  },
  data() {
    return {
      swiperOptions: {
        spaceBetween: 1000,
        loop: true,
        effect: 'fade',
        fadeEffect: {
          crossFade: true,
        },
        pagination: {
          el: '.main-slider__pagination',
          type: 'bullets',
          clickable: true,
        },
        navigation: {
          nextEl: '.main-slider__arrow--next',
          prevEl: '.main-slider__arrow--prev',
        },
      },
    };
  },
};
</script>

