<template>
  <div
    class="letters-list"
    :class="{'letters-list--default':!light,'letters-list--light':light,}"
  >
    <ul>
      <li
        v-for="letter in lettersArray"
        :key="letter"
      >
        <a
          :class="{'active':value===letter}"
          @click="$emit('input', letter)"
        >
          {{ letter }}
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'LettersList',
  props:{
    value:{
      type:String,
      default:()=>'',
    },
    light:{
      type:Boolean,
      default:()=>false,
    },
  },
  data(){
    return {
      lettersArray:[ '', '', '', '', '', '/', '', '', '/', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '' ],
    };
  },
};
</script>

<style lang="scss" src="./LettersList.scss" scoped/>