export const headerModule = {
  state: {
    navLinks: [
      {
        href: '/our-brands',
        name: ' ',
      },
      {
        href: '/stocks',
        name: '',
      },
      {
        href: '/abc-health',
        name: ' ',
      },
      {
        href: '/articles',
        name: '',
      },
      {
        href: '/about-us',
        name: ' ',
      },
      {
        href: '/where-buy',
        name: ' ',
        mapIcon: true,
      },
      {
        href: '/contacts',
        name: '',
      },
    ],
    searchQuery:'',
    isFavoriteShow:false,
    filterItems: [
      {
        name: ' ',
        value: 'vector',
      },
      {
        name: '  ',
        value: 'component',
      },
      {
        name: ' ',
        value: 'name',
      },
    ],

  },
  getters: {
    navLinks:(state) => state.navLinks,
    searchQuery:(state) => state.searchQuery,
    isFavoriteShow:(state) => state.isFavoriteShow,
    filterItems:(state) => state.filterItems,
  },
  mutations: {
    updateNavLinks: (state, payload) => {
      state.navLinks = payload;
    },
    updateSearchQuery: (state, payload) => {
      state.searchQuery = payload;
    },
    updateIsFavoriteShow: (state, payload) => {
      state.isFavoriteShow = payload;
    },
  },
  actions: {
    setSearchQuery({ commit }, query) {
      commit('updateSearchQuery', query);
    },
    setIsFavoriteShow({ commit }, query) {
      commit('updateIsFavoriteShow', query);
    },
  },
};
